@import url(https://fonts.googleapis.com/css?family=Chakra+Petch|Amatic+SC|Gamja+Flower);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: url(/pomodoro/static/media/wet-tomato.f021c6be.jpg);
  background-size: cover;
  background-position: 70%;
  overflow: hidden;
}

/* desktop */
@media (min-width: 768px) {
  body {
    background: url(/pomodoro/static/media/tomato-bkg.a39d988a.jpg) no-repeat;
    background-size: cover;
  }
}

.app-container{
  border: 2px solid black;
  width: 86vw;
  height: 90vh;
  position: relative;
  margin: 5vh auto;
  background: rgb(255, 237, 231, 0.9);
  border-radius: 3%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

/* desktop */
@media (min-width: 768px) {
  .app-container{
    width: 30vw;
    height: 80vh;
    margin: 10vh auto;

  }
}

.time-interval-container{
  width: 100%;
  height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

}

.title{
  font-family: 'Amatic SC';
  font-size: 3rem;
  text-align: center;
  margin: 4vh auto;

}

.interval-buttons-container{
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-self: center;
  margin-bottom: 5vh;
}


.all-button{
  border: none;
  border-radius: 8%;
  background: rgb(255, 72, 72, 0.4);
  font-family: 'Amatic SC';
  font-size: 1.4rem;
}

.interval-button {

}

.display-container{
  width: 100%;
  height: 30vh;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-around;
  align-items: center;
  text-align: center;

  
}

.session-title{
  font-family: 'Gamja Flower';
  font-size: 1.6rem;
  margin: 0;
}

.countdown-timer{
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 30% 90%;
  height: 100%;
  width: 100%;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Chakra Petch';
  font-size: 1.4rem;
  z-index: 1;
}


.controls-container{
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 5vh 0;
}

.control-button{
  font-size: 1.6rem;
}

.settings-container{
  align-self: center;
  margin-bottom: 5vh;
}

.settings-button{
  border-radius: 50%;
  border: none;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 80%;
  height: 10vh;
  width: 10vh;
  margin: 0;
}

.input-container{

}

label {
  display: inline-block;
  font-family: 'Amatic SC';
  font-size: 1.8rem;
  margin: 0;
  padding: 0;
}

input {
  font-family: 'Chakra Petch';
  border: none;
  border-radius: 3%;
  text-align: right;
  height: 2rem;
  width: 80%;
  font-size: 1.4rem;
}

.hide {
  display: none;
}

.show {
  position: absolute;
  top: 15vh;
  left: 0;
  margin: 0 5%;
  background: rgb(255, 237, 231);
  box-shadow: inset 0 0 2px 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  z-index: 1;
  width: 90%;
  height: 75%;
  border-radius: 3%;
}

.confirm-button {
  height: 8vh;
  width: 8vh;
  background: url(data:image/png;base64,) no-repeat center center;
  border-radius: 50%;
  border: none;
  margin-bottom: 2vh;

}
